.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  padding: 10px 20px;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #f3f3f3;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
  display: block;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

